import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";
import NumberFormat from "../../../App/NumberFormat";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col,
  Divider
  // message
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/payablePaymentReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { getFileName } from "../../../../helpers/helper";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectPP = "/dashboard/financials/payable-payments";
const redirectCF = "/dashboard/financials/cash-managements";

class PayablePaymentReport extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      id_cash_bank: null,
      status: null,
      cash_management_status: null,
      transaction_status: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
      sort_by: "transaction_date",
      sort_type: "asc"
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getPayablePaymentReports } = this.props;
    getPayablePaymentReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.payablePaymentReport.error) {
      notification("error", this.props.payablePaymentReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.payablePaymentReport.success) {
      notification("success", this.props.payablePaymentReport.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getPayablePaymentReports } = this.props;
    let filter = this.filterValue();

    getPayablePaymentReports(1, value, filter, this.state.filter);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getPayablePaymentReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        id_cash_bank: null,
        status: null,
        cash_management_status: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getPayablePaymentReports();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else if (name === "id_location") {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value,
          id_cash_bank: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.id_cash_bank) {
      string
        ? (string += "&id_cash_bank=" + filter.id_cash_bank)
        : (string += "id_cash_bank=" + filter.id_cash_bank);
    }
    if (filter.status) {
      string
        ? (string += "&status=" + filter.status)
        : (string += "status=" + filter.status);
    }
    if (filter.cash_management_status) {
      string
        ? (string += "&cash_management_status=" + filter.cash_management_status)
        : (string += "cash_management_status=" + filter.cash_management_status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }

    string
      ? (string +=
          "&sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type)
      : (string +=
          "sort_by=" + filter.sort_by + "&sort_type=" + filter.sort_type);

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getPayablePaymentReports } = this.props;
    //const { current_page } = this.props.payablePaymentReport;
    let filter = this.filterValue();

    this.setState({ search: null });
    getPayablePaymentReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPayablePaymentReports } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        id_cash_bank: null,
        status: null,
        cash_management_status: null,
        transaction_status: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss"),
        sort_by: "transaction_date",
        sort_type: "asc"
      }
    });
    this.setState({ search: null });
    getPayablePaymentReports();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activatePayablePaymentReport(id);
    } else {
      this.props.deactivatePayablePaymentReport(id);
    }
  };

  // // Handle pagination
  // handlePageChange = page => {
  //   const { getPayablePaymentReports } = this.props;
  //   const { search, filter } = this.props.payablePaymentReport;
  //   getPayablePaymentReports(page, search, filter);
  // };

  render() {
    const columns = [
      {
        title: getLanguage({ id: "date" }),
        // date: true,
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 7,
        render: text => {
          if (text === "Total") {
            return {
              children: (
                <span style={{ marginLeft: "50%" }}>
                  <strong>{text}</strong>
                </span>
              ),
              props: {
                colSpan: 7
              }
            };
          } else {
            return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "payablePayments.payablePaymentNo" }),
        dataIndex: "payable_payment_no",
        rowKey: "payable_payment_no",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            let className;
            switch (row.status) {
              case 1:
                className = "pending";
                break;
              case 2:
                className = "processed";
                break;
              case 3:
                className = "cancelled";
                break;
              default:
                className = "pending";
                break;
            }

            return (
              <div style={{ display: "flex", height: "20px" }}>
                <StatusBadge className={className} />
                <TransNo>
                  <Link to={`${redirectPP}/update/${row.id_payable_payment}`}>
                    {text}
                  </Link>
                </TransNo>
              </div>
            );
          }
        }
      },
      {
        title: getLanguage({ id: "cashManagements.no" }),
        dataIndex: "cash_management_no",
        rowKey: "cash_management_no",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            let className;
            switch (row.cash_management_status) {
              case 1:
                className = "new";
                break;
              case 2:
                className = "ordered";
                break;
              case 3:
                className = "cancelled";
                break;
              default:
                className = "new";
                break;
            }

            return (
              <div style={{ display: "flex", height: "20px" }}>
                <StatusBadge className={className} />
                <TransNo>
                  <Link to={`${redirectCF}/update/${row.id_cash_management}`}>
                    {text}
                  </Link>
                </TransNo>
              </div>
            );
          }
        }
      },
      {
        title: getLanguage({ id: "suppliers.pageHeader" }),
        dataIndex: "supplier_name",
        rowKey: "supplier_name",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "racks.add.location" }),
        dataIndex: "location_name",
        rowKey: "location_name",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.cashBankReport" }),
        dataIndex: "cash_bank.cash_bank_code",
        rowKey: "cash_bank.cash_bank_code",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "status_string",
        rowKey: "status_string",
        //width: "10%",
        spanCheck: "string",
        spanCheckValue: "Total",
        spanValue: 0,
        align: "center",
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return {
              props: {
                colSpan: 0
              }
            };
          } else {
            let className;
            let display;
            switch (row.status) {
              case 1:
                className = "pending";
                display = getLanguage({ id: "pending" });
                break;
              case 2:
                className = "processed";
                display = getLanguage({ id: "processed" });
                break;
              case 3:
                className = "cancelled";
                display = getLanguage({ id: "cancelled" });
                break;
              default:
                className = "pending";
                display = getLanguage({ id: "pending" });
                break;
            }

            return <StatusTag className={className}>{display}</StatusTag>;
          }
        }
      },
      // {
      //   title: getLanguage({ id: "totalDiscountAmount" }),
      //   dataIndex: "total_discount_amount",
      //   rowKey: "total_discount_amount",
      //   width: "10%",
      //   parse: true,
      //   align: "right",
      //   render: (text, row) => {
      //     if (row.transaction_date === "Total") {
      //       return (
      //         <strong>
      //           <NumberFormat value={parseFloat(text)} />
      //         </strong>
      //       );
      //     } else {
      //       return <NumberFormat value={parseFloat(text)} />;
      //     }
      //   }
      // },
      {
        title: getLanguage({ id: "totalMemoAmount" }),
        dataIndex: "total_memo_amount",
        rowKey: "total_memo_amount",
        width: "10%",
        parse: true,
        align: "right",
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          } else {
            return <NumberFormat value={parseFloat(text)} />;
          }
        }
      },
      {
        title: getLanguage({ id: "totalAmount" }),
        dataIndex: "total_amount",
        rowKey: "total_amount",
        width: "10%",
        parse: true,
        align: "right",
        render: (text, row) => {
          if (row.transaction_date === "Total") {
            return (
              <strong>
                <NumberFormat value={parseFloat(text)} />
              </strong>
            );
          } else {
            return <NumberFormat value={parseFloat(text)} />;
          }
        }
      }
    ];
    // console.log(this.props.payablePaymentReport);
    // Data setup
    const { payablePaymentReport } = this.props;
    const {
      payable_payment_reports,
      locations,
      suppliers,
      cash_banks,
      filter,
      filter_select
    } = payablePaymentReport;
    //console.log(this.props.payablePaymentReport.base_currency); //console.log(this.state);

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];
    const { loading, loadingForeign } = this.props.payablePaymentReport;

    let dataSource = [];
    let total = {
      transaction_date: "Total",
      total_memo_amount: 0,
      total_discount_amount: 0,
      total_amount: 0
    };

    payable_payment_reports.map(data => {
      let status_string;

      if (data.status === 2) {
        status_string = getLanguage({ id: "processed" });
      } else if (data.status === 3) {
        status_string = getLanguage({ id: "cancelled" });
      } else {
        status_string = getLanguage({ id: "pending" });
      }

      dataSource.push({
        ...data,
        status_string
      });

      total.total_memo_amount += parseFloat(data.total_memo_amount);
      total.total_discount_amount += parseFloat(data.total_discount_amount);
      total.total_amount += parseFloat(data.total_amount);
    });

    dataSource.push(total);

    const fileName = getLanguage({ id: "payablePaymentReports.pageHeader" });
    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="payablePaymentReports.pageHeader" />}
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={<IntlMessages id="payablePaymentReports.pageHeader" />}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "zoom: 0.87;" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              scale={0.4}
              title="payablePaymentReports.pageHeader"
            />

            <ExcelWrapper
              title="payablePaymentReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "payablePayments.payablePaymentNo" }) +
                ", " +
                getLanguage({ id: "cashManagements.no" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.searchFilter" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="suppliers.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.supplier" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {suppliers.map((data, key) => (
                        <Option key={key} value={data.id_supplier}>
                          {`${data.supplier_code} (${data.supplier_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="payablePayments.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="cashBanks.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_cash_bank"
                      )}
                      value={
                        this.state.filter.id_cash_bank
                          ? this.state.filter.id_cash_bank
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.cashBank" />
                        ) : (
                          <IntlMessages id="noLocation" />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loading}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                      //showArrow={false}
                    >
                      {cash_banks.map((data, key) =>
                        this.state.filter.id_location === data.id_location ? (
                          <Option key={key} value={data.id_cash_bank}>
                            {data.bank_account_number === null
                              ? data.cash_bank_code
                              : data.cash_bank_code +
                                " (" +
                                data.bank_account_number +
                                ")"}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "24px" }}
                >
                  <Col span={12}>
                    <Row justify="start" gutter={1} type="flex">
                      <Col span={12}>
                        <label>
                          <IntlMessages id="stockValuationReports.sortBy" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_by"
                          )}
                          value={
                            this.state.filter.sort_by
                              ? this.state.filter.sort_by
                              : "transaction_date"
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="transaction_date">
                            <IntlMessages id="payablePaymentReports.filter.date" />
                          </Option>
                          <Option key={2} value="payable_payment_no">
                            <IntlMessages id="payablePayments.payablePaymentNo" />
                          </Option>
                          <Option key={3} value="location_name">
                            <IntlMessages id="sidebar.location" />
                          </Option>
                          <Option key={4} value="supplier_name">
                            <IntlMessages id="sidebar.supplier" />
                          </Option>
                        </Select>
                      </Col>
                      <Col span={12}>
                        <label>
                          <IntlMessages id="sortType" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "sort_type"
                          )}
                          value={
                            this.state.filter.sort_type
                              ? this.state.filter.sort_type
                              : "asc"
                          }
                          style={{ width: "90%" }}
                          // placeholder={
                          //   <IntlMessages id="stockValuationReports.sortBy.select" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value="asc">
                            <IntlMessages id="ascending" />
                          </Option>
                          <Option key={2} value="desc">
                            <IntlMessages id="descending" />
                          </Option>
                        </Select>
                      </Col>
                    </Row>
                  </Col>
                  <Col span={12}>
                    <Row gutter={1} type="flex">
                      <Col span={8}>
                        <label>Status</label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "status"
                          )}
                          value={
                            this.state.filter.status
                              ? this.state.filter.status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder={getLanguage({ id: "status" })}
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "pending" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "processed" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "cancelled" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.only" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="clicksearchFilter" />} />
            ) : payable_payment_reports.length === 0 &&
              !this.props.payablePaymentReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="payablePaymentReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    payablePaymentReport: state.payablePaymentReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(PayablePaymentReport);
